    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default { 
		data() {
			return {
				list:[],
				loadingText: '正在加载...',
				hasmore:true,
				curpage:1,
				num:0,
				placeholder: '输入会员名称',
				keyword:''
			}				
		},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad() {
			console.log('页面加载');
			this.loadInvietNum();
			this.loadData();
		},
         onReachBottom() {
			 this.curpage++;
			 this.loadData();
         },
        methods: {  
			loadInvietNum(){
				let _this = this;
				let postData = {};
				if(_this.keyword!=''){
					postData = {keyword:_this.keyword};
				}
				_this.$Request.post('member_invite/minvitenum',postData).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.num=res.datas.num;
					}
				});
			},
            async loadData() {  
				var _this = this;
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					if(!this.hasmore){
						this.loadingText = '暂无更多信息信息';
						return false;
					}
					let postData = {curpage:_this.curpage};
					if(_this.keyword!=''){
						postData = {curpage:_this.curpage,keyword:_this.keyword};
					}
					_this.$Request.post('member_invite/memberlist',postData).then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.hasmore =res.hasmore;
							console.log(_this.hasmore);
							let _conslist = res.datas.list;
							for(var i=0;i<_conslist.length;i++){
								_this.list.push(_conslist[i]);
							}
							if(!this.hasmore){
								this.loadingText = '暂无更多信息信息';
								return false;
							}
						}
					});
				}
            },
			submitSearch: function() {
				this.hasmore=true;
				this.loadingText = '正在加载...';
				this.list=[];
				this.loadInvietNum();
				this.loadData();
			},
			 
        }  
    }  
